<template>
  <div v-if="commenters.list && commenters.list.length > 0" class="flex flex-wrap justify-center" id="commenter">
    <div class="flex flex-col items-center justify-between bg-black bg-opacity-50 rounded-2xl z-20 mt-20 mx-1 px-2 w-fit relative" v-for="commenter in commenters.list" :key="commenter.id">
      <div class="group rounded-full">
        <the-avatar class="transform -translate-y-16 group-hover:blur-sm group-hover:saturate-50 group-hover:brightness-50"
                    :src="commenter.image.content" :alt="commenter.image.description" size="2xl" hasRing/>
        <span class="absolute top-0 left-1/2 transform -translate-x-1/2 text-xl font-semibold w-48 text-center invisible group-hover:visible">{{ commenter.name }}</span>
      </div>
      <span class="absolute block w-full text-center h-16 text-lg font-semibold top-44">{{commenters.COMMENT_DATE}}: {{ commenter.date }}</span>
      <div>
        <span class="commenter-tags" v-for="tag in commenter.tags" :key="tag">{{ tag }}</span>
      </div>
      <p class="text-md font-sans mt-5 px-3 text-justify indent-4">
        {{ commenter.shortDesc }}
        <more-detail-icon v-if="commenter.fullDesc" class="w-10 h-10 cursor-pointer hover:bg-[#632E99] hover:bg-indigo-200 hover:rounded-full p-2 mb-3"
                          icon-name="More Details (full description)" icon-color="white"
                          @click="openMoreDetails(commenter.fullDesc)"/>
      </p>
    </div>
    <the-modal v-model="toggleModal" :title="commenters.FULL_DESCRIPTION" :full-screen="$breakpoints.isSmAndDown">
      <p class="text-black px-4 mb-4 text-justify indent-4">{{ fullDesc }}</p>
    </the-modal>
  </div>
</template>

<script>
import MoreDetailIcon from "@/landing/components/UI/icons/MoreDetailIcon";
import TheModal from "@/landing/components/UI/TheModal";
import TheAvatar from "@/landing/components/UI/TheAvatar";

export default {
  name: "CommentersInfo",
  components: {TheAvatar, MoreDetailIcon, TheModal},
  data() {
    return {
      toggleModal: false,
      fullDesc: null
    }
  },
  computed: {
    commenters() {
      return this.$store.getters["commenterData"];
    }
  },
  methods: {
    openMoreDetails(fullDesc) {
      this.toggleModal = true;
      this.fullDesc = fullDesc;
    }
  }
}
</script>

<style scoped>
.commenter-tags {
  @apply text-black font-bold m-1 px-2 py-1 bg-gray-400 inline-block rounded-xl;
}
</style>