<template>
  <img :src="src" :alt="alt"  class="p-1" :class="[sizeClasses, roundedClass, ringClasses]">
</template>

<script>
export default {
  name: "TheAvatar",
  props: {
    src: {
      required: true,
      type: String,
      validator: value => value.length > 0
    },
    alt: {
      type: String,
      default: 'The Avatar'
    },
    size: {
      type: String,
      default: 'sm',
      validator: value => ['xs', 'sm', 'md', 'lg', 'xl', '2xl'].includes(value)
    },
    isRoundedFull: {
      default: false,
      type: Boolean
    },
    hasRing: {
      default: false,
      type: Boolean
    }
  },
  computed: {
    sizeClasses() {
      switch (this.size) {
        case 'xs': return "w-10 h-10";
        case 'md': return "w-24 h-24";
        case 'lg': return "w-32 h-32";
        case 'xl': return "w-40 h-40";
        case '2xl': return "w-56 h-56";
        default: return "w-16 h-16";
      }
    },
    roundedClass() {
      return this.isRoundedFull ? "rounded-full" : "";
    },
    ringClasses() {
      return this.hasRing ? "ring-2 ring-zinc-300" : "";
    }
  }
}
</script>