<template>
  <the-card id="education">
    <template #title>{{ educations.EDUCATION_HEADER_TITLE }}</template>
    <div class="my-5"></div>
    <div class="flex mb-2 last-of-type:mb-0" v-for="education in educations.list" :key="education.id">
      <div class="w-3/12 md:w-2/12 after:block after:bg-black after:w-[1px] after:h-[calc(100%-5rem)] md:after:h-[calc(100%-7rem)] after:mx-auto after:my-2">
        <the-avatar :src="education.logo.content" :alt="education.logo.description" class="mx-auto w-16 h-16 md:w-24 md:h-24"/>
      </div>
      <div class="w-9/12 md:w-10/12">
        <div class="text-lg text-black font-extrabold">{{ education.university }}</div>
        <div class="text-md text-black">{{education.fromDate}} - {{education.toDate}}</div>
        <div class="text-sm text-black font-semibold mb-3">{{ education.degree }} - {{ education.major }}</div>
        <p class="text-md text-black text-justify indent-4 px-3">
          {{ education.shortDesc }}
          <more-detail-icon v-if="education.fullDesc" class="w-10 h-10 cursor-pointer hover:bg-[#632E99] hover:bg-indigo-200 hover:rounded-full p-2"
                            icon-name="More Details (full description)" icon-color="#14236C"
                            @click="openMoreDetails(education.fullDesc)"/>
        </p>
      </div>
    </div>
    <the-modal v-model="toggleModal" :title="educations.FULL_DESCRIPTION" :full-screen="$breakpoints.isSmAndDown">
      <p class="text-black px-4 text-justify indent-4">{{ fullDesc }}</p>
    </the-modal>
  </the-card>
</template>

<script>
import TheCard from "@/landing/components/UI/TheCard";
import TheAvatar from "@/landing/components/UI/TheAvatar";
import TheModal from "@/landing/components/UI/TheModal";
import MoreDetailIcon from "@/landing/components/UI/icons/MoreDetailIcon";

export default {
  name: "EducationsInfo",
  components: {MoreDetailIcon, TheModal, TheAvatar, TheCard},
  data() {
    return {
      toggleModal: false,
      fullDesc: null
    }
  },
  computed: {
    educations() {
      return this.$store.getters["educationsData"];
    }
  },
  methods: {
    openMoreDetails(fullDesc) {
      this.toggleModal = true;
      this.fullDesc = fullDesc;
    }
  }
}
</script>