<template>
  <div class="about-me-container break-words">
    <p v-if="$breakpoints.isLgAndUp" class="text-justify indent-4">
      {{ rawText }}
    </p>
    <p v-else-if="rawText.length > this.defaultShowLimit" class="text-justify indent-4">
      {{ text }}
      <plus-icon v-if="!showAll" class="show-more" icon-color="black" icon-name="Show More" @click="showAll = !showAll" />
      <minus-icon v-else class="show-less" icon-color="black" icon-name="Show Less" @click="showAll = !showAll" />
    </p>
    <p v-else class="text-justify indent-4">
      {{ text }}
    </p>
  </div>
</template>

<script>
import PlusIcon from "@/landing/components/UI/icons/PlusIcon";
import MinusIcon from "@/landing/components/UI/icons/MinusIcon";

export default {
  name: "AboutMe",
  components: {MinusIcon, PlusIcon},
  props: {
    rawText: {
      required: true,
      type: String
    },
    defaultShowLimit: {
      type: Number,
      default: 250
    }
  },
  data: () => ({
    showAll: false
  }),
  computed: {
    text() {
      if (this.showAll)
        return this.rawText;
      else if (this.rawText.length > this.defaultShowLimit)
        return this.rawText.slice(0, this.rawText.slice(0, this.defaultShowLimit).lastIndexOf(" "));
      return this.rawText;
    }
  },
}
</script>

<style scoped>
.about-me-container {
  @apply mx-3 px-3 pb-6 text-center bg-white bg-opacity-30 border-2 rounded-lg;
}
.show-more {
  @apply w-5 h-5 inline-block cursor-pointer bg-white bg-opacity-50 rounded-full align-middle;
}
.show-less {
  @apply w-5 h-5 inline-block cursor-pointer bg-white bg-opacity-50 rounded-full align-middle;
}
</style>