<template>
  <the-card id="skills">
    <template #title>{{ skillCategories.SKILL_HEADER_TITLE }}</template>
    <div class="my-5"></div>
    <div class="flex flex-col">
      <div class="mb-4 last-of-type:mb-0" v-for="skillCategory in skillCategories.list" :key="skillCategory.id">
        <div class="inline-block bg-ham-dark-blue text-white  text-center mb-7 p-2 rounded-full w-full md:w-1/2 ">{{ skillCategory.title }}</div>
        <div class="flex flex-wrap justify-center">
          <div class="mx-5 mb-6 flex flex-col items-center w-1/3 lg:w-1/4 xl:w-1/5 " v-for="skill in skillCategory.skills" :key="skill.id">
            <div class="w-28 h-28 rounded-full flex justify-center items-center relative" :style="[circularLevel(skill.level)]">
              <div class="bg-white rounded-full">
                <the-avatar :src="skill.image.content" :alt="skill.image.description" is-rounded-full size="md" />
              </div>
              <div class="w-11 h-11 bg-ham-dark-blue text-xs font-extrabold rounded-full absolute -top-3 -right-1 flex justify-center items-center">{{skill.level}}%</div>
              <horizontal-dots-icon v-if="skill.description" class="w-7 h-7 bg-violet-100 hover:bg-[#632E99] hover:bg-indigo-200 rounded-full absolute bottom-3 -left-4 flex justify-center items-center cursor-pointer"
                                    icon-name="More Detail (skill description)" icon-color="#002060"
                                    @click="openMoreDetails(skill.description)" />
            </div>
            <span class="text-black text-center">{{ skill.title }}</span>
          </div>
        </div>
      </div>
      <the-modal v-model="toggleModal" :title="skillCategories.FULL_DESCRIPTION">
        <p class="text-black px-4 text-justify indent-4">{{ skillDescription }}</p>
      </the-modal>
    </div>
  </the-card>
</template>

<script>
import TheCard from "@/landing/components/UI/TheCard";
import TheAvatar from "@/landing/components/UI/TheAvatar";
import HorizontalDotsIcon from "@/landing/components/UI/icons/HorizontalDotsIcon";
import TheModal from "@/landing/components/UI/TheModal";

export default {
  name: "SkillsInfo",
  components: {HorizontalDotsIcon, TheCard, TheAvatar, TheModal},
  data() {
    return {
      toggleModal: false,
      skillDescription: null
    }
  },
  computed: {
    skillCategories() {
      return this.$store.getters["skillCategoryData"];
    },
  },
  methods: {
    circularLevel(level) {
      return `background: conic-gradient(#6c2f9e ${(+level / 100) * 360}deg, white 0deg)`;
    },
    openMoreDetails(description) {
      this.toggleModal = true;
      this.skillDescription = description;
    }
  }
}
</script>