<template>
  <div class="w-full md:w-1/2 fixed top-0 z-50" :class="[notifyPositionEnd]" :dir="currentLanguage.direction">
    <transition-group name="notification">
      <notification-message
          v-for="notification in notifications"
          :key="notification.id"
          :notification="notification"/>
    </transition-group>
  </div>
</template>

<script>
import NotificationMessage from "@/landing/components/UI/NotificationMessage";

export default {
  name: "NotificationList",
  components: {
    NotificationMessage
  },
  computed: {
    currentLanguage() {
      return this.$store.getters["languageData"]
    },
    notifications() {
      return this.$store.getters["notify/notificationList"];
    },
    notifyPositionEnd() {
      return this.$store.getters["languageData"].direction === 'rtl' ? "right-0 md:mr-3" : "left-0 md:ml-3";
    },
  }
}
</script>

<style scoped>
.notification-enter-active,
.notification-leave-active {
  transition: all 0.5s ease;
}

.notification-leave-to,
.notification-enter-from {
  opacity: 0;
  transform: translateY(-100px);
}

.notification-leave-from,
.notification-enter-to {
  opacity: 1;
  transform: translateY(0);
}
</style>