import {createStore} from 'vuex'
import axios from "axios";
import NotificationStore from "@/landing/store/modules/notificationStore";

const url = `https://resume.javagym.ir/api`;
const clientId = 'fecef976-d662-49fd-92b2-75a0b6da1cc0';

export default createStore({
    modules: {
        notify: NotificationStore,
    },
    state: {
        loading: true,
        problem: true,
        resume: [],
        selectedLanguage: null,
    },
    getters: {
        loadingState(state) {
            return state.loading;
        },
        problemState(state) {
            return state.problem;
        },
        languageBarData(state) {
            return state.resume.map(detail => detail.language);
        },
        languageData(state) {
            return state.resume.find(detail => detail.language.id === state.selectedLanguage.id).language;
        },
        personalDetailData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                ...detail.personalDetail,
                pdf: detail.pdf,
                DOWNLOAD_PDF_BTN: detail.keyValues.DOWNLOAD_PDF_BTN || 'Resume PDF',
                CONTACT_ME_BTN: detail.keyValues.CONTACT_ME_BTN || 'Contact Me',
                MY_INTERESTS: detail.keyValues.MY_INTERESTS || 'My Interests',
                COPY_TO_CLIPBOARD_TITLE: detail.keyValues.COPY_TO_CLIPBOARD_TITLE || 'Copy To Clipboard',
                COPY_TO_CLIPBOARD_MESSAGE: detail.keyValues.COPY_TO_CLIPBOARD_MESSAGE || 'Text Successfully Copied!',
            };
        },
        educationsData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                list: [...detail.educations],
                EDUCATION_HEADER_TITLE: detail.keyValues.EDUCATION_HEADER_TITLE || 'Education',
                FULL_DESCRIPTION: detail.keyValues.FULL_DESCRIPTION || 'Full Description',
            };
        },
        experiencesData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                list: [...detail.experiences],
                EXPERIENCE_HEADER_TITLE: detail.keyValues.EXPERIENCE_HEADER_TITLE || 'Experience',
                FULL_DESCRIPTION: detail.keyValues.FULL_DESCRIPTION || 'Full Description',
            };
        },
        skillCategoryData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                list: [...detail.skillCategories],
                SKILL_HEADER_TITLE: detail.keyValues.SKILL_HEADER_TITLE || 'Skills',
                FULL_DESCRIPTION: detail.keyValues.FULL_DESCRIPTION || 'Full Description',
            };
        },
        portfolioData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                list: [...detail.portfolios],
                PORTFOLIO_HEADER_TITLE: detail.keyValues.PORTFOLIO_HEADER_TITLE || 'Portfolios',
                PORTFOLIO_RELEASE_DATE: detail.keyValues.PORTFOLIO_RELEASE_DATE || 'Release Date',
                MORE_DETAILS: detail.keyValues.MORE_DETAILS || 'More Details',
            };
        },
        commenterData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                list: [...detail.commenters],
                COMMENTER_HEADER_TITLE: detail.keyValues.COMMENTER_HEADER_TITLE || 'Commenters',
                COMMENT_DATE: detail.keyValues.COMMENT_DATE || 'Comment Date',
                FULL_DESCRIPTION: detail.keyValues.FULL_DESCRIPTION || 'Full Description',
            };
        },
        contactFormData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                CONTACT_FORM_TITLE: detail.keyValues.CONTACT_FORM_TITLE || 'Contact me',
                CONTACT_FORM_SUBTITLE: detail.keyValues.CONTACT_FORM_SUBTITLE || 'In addition to the above communication ways, you can contact me through this form',
                CONTACT_FORM_FULL_NAME_LABEL: detail.keyValues.CONTACT_FORM_FULL_NAME_LABEL || 'Full name',
                CONTACT_FORM_FULL_NAME_REQUIRED: detail.keyValues.CONTACT_FORM_FULL_NAME_REQUIRED || 'You must send your full name',
                CONTACT_FORM_EMAIL_LABEL: detail.keyValues.CONTACT_FORM_EMAIL_LABEL || 'Email address',
                CONTACT_FORM_EMAIL_REQUIRED: detail.keyValues.CONTACT_FORM_EMAIL_REQUIRED || 'You must send your email',
                CONTACT_FORM_EMAIL_NOT_VALID: detail.keyValues.CONTACT_FORM_EMAIL_NOT_VALID || 'You must send a valid email',
                CONTACT_FORM_MESSAGE_LABEL: detail.keyValues.CONTACT_FORM_MESSAGE_LABEL || 'Your message',
                CONTACT_FORM_MESSAGE_REQUIRED: detail.keyValues.CONTACT_FORM_MESSAGE_REQUIRED || 'You must send your message',
                CONTACT_FORM_SUBMIT_BTN: detail.keyValues.CONTACT_FORM_SUBMIT_BTN || 'Send',
            };
        },
        stickySideBarData(state) {
            const detail = state.resume.find(detail => detail.language.id === state.selectedLanguage.id);
            return {
                currentDirection: detail.language.direction,
                navigations: [
                    ...(detail.personalDetail ? [{id: '#personalInfo', icon: 'PersonIcon', title: detail.keyValues.PERSONAL_INFO_HEADER_TITLE || 'Personal Information'}] : []),
                    ...(detail.educations.length > 0 ? [{id: '#education', icon: 'EducationIcon', title: detail.keyValues.EDUCATION_HEADER_TITLE || 'Education'}] : []),
                    ...(detail.experiences.length > 0 ? [{id: '#experience', icon: 'EngineerIcon',  title: detail.keyValues.EXPERIENCE_HEADER_TITLE || 'Experience'}] : []),
                    ...(detail.skillCategories.length > 0 ? [{id: '#skills', icon: 'SkillsIcon',  title: detail.keyValues.SKILL_HEADER_TITLE || 'Skills'}] : []),
                    ...(detail.portfolios.length > 0 ? [{id: '#portfolio', icon: 'PortfolioIcon',  title: detail.keyValues.PORTFOLIO_HEADER_TITLE || 'Portfolios'}] : []),
                    ...(detail.commenters.length > 0 ? [{id: '#commenter', icon: 'EngineerIcon',  title: detail.keyValues.COMMENTER_HEADER_TITLE || 'Commenters'}] : []),
                    {id: '#contact', icon: 'ContactIcon',  title: detail.keyValues.CONTACT_FORM_TITLE || 'Contact me'},
                ],
            };
        }
    },
    actions: {
        getResumeData(context) {
            context.state.loading = true;
            context.state.problem = true;
            axios.post(`${url}/landingPage`, {clientId})
                .then(res => {
                    context.state.resume = res.data;
                    context.state.selectedLanguage = context.state.resume[0].language;
                    context.state.problem = false;
                }).catch(() => context.state.problem = true).finally(() => context.state.loading = false);
        },
        selectLanguage(context, language) {
            context.state.selectedLanguage = language;
        },
        submitContact(context, data) {
            const detail = context.state.resume.find(detail => detail.language.id === context.state.selectedLanguage.id);
            axios.post(`${url}/contact`, {clientId, ...data})
                .then(() => context.dispatch("notify/addDefaultSuccess", {
                    title: detail.keyValues.CONTACT_FORM_SUCCESS_TITLE || "Operation was successful",
                    message: detail.keyValues.CONTACT_FORM_SUCCESS_MESSAGE || "Your message successfully saved"
                }))
                .catch(() => context.dispatch("notify/addDefaultError", {
                    title: detail.keyValues.CONTACT_FORM_ERROR_TITLE || "Operation was failed",
                    message: detail.keyValues.CONTACT_FORM_ERROR_MESSAGE || "Sorry! some issue happened"
                }));
        }
    },
})
