export default {
    namespaced: true,
    state: {
        notifications: []
    },
    getters: {
        notificationList(state) {
            return state.notifications;
        }
    },
    mutations: {
        pushNotification(state, notification) {
            state.notifications.push({
                id: (Math.random().toString(36) + Date.now().toString(36)).substring(2),
                ...notification
            });
        },
        popNotification(state, notificationToRemove) {
            state.notifications = state.notifications.filter(notification => notification.id !== notificationToRemove.id);
        }
    },
    actions: {
        addDefaultError(context, textPayload) {
            context.commit('pushNotification', {
                type: 'error', title: textPayload.title, message: textPayload.message, timeout: 4000
            });
        },
        addDefaultSuccess(context, textPayload) {
            context.commit('pushNotification', {
                type: 'success', title: textPayload.title, message: textPayload.message, timeout: 4000
            });
        },
        removeNotification(context, notificationToRemove) {
            context.commit('popNotification', notificationToRemove);
        }
    }
}