<template>
  <the-card class="contact-card" id="contact">
    <template #title >
      <div class="text-black text-center mb-3">{{ contactForm.CONTACT_FORM_TITLE }}</div>
    </template>
    <template #subtitle>
      <span class="text-black px-2">{{ contactForm.CONTACT_FORM_SUBTITLE }}</span>
    </template>

    <div class="mt-5">
      <div class="grid md:grid-cols-2 md:gap-6">
        <div class="relative z-0 w-full mb-6 group">
          <input type="text" name="floating_full_name" id="floating_full_name" class="text-input text-input-border peer"
                 placeholder=" " v-model="formData.fullName" @blur="v$.formData.fullName.$touch" />
          <label for="floating_full_name" class="text-label" :class="[labelPositionEnd]">{{ contactForm.CONTACT_FORM_FULL_NAME_LABEL }}</label>
          <div class="text-rose-900 px-4 mt-1" v-for="error in v$.formData.fullName.$errors" :key="error.$uid">{{ error.$message }}</div>
        </div>
        <div class="relative z-0 w-full mb-6 group">
          <input type="email" name="floating_email" id="floating_email" class="text-input text-input-border peer" placeholder=" "
                 v-model="formData.email" @blur="v$.formData.email.$touch" />
          <label for="floating_email" class="text-label" :class="[labelPositionEnd]">{{ contactForm.CONTACT_FORM_EMAIL_LABEL }}</label>
          <div class="text-rose-900 px-4 mt-1" v-for="error in v$.formData.email.$errors" :key="error.$uid">{{ error.$message }}</div>
        </div>
      </div>
      <div class="relative z-0 w-full mb-6 group">
        <textarea type="textarea" name="floating_message" id="floating_message" class="text-input text-input-border peer" placeholder=" "
                  rows="5" v-model="formData.message" @blur="v$.formData.message.$touch" />
        <label for="floating_message" class="text-label" :class="[labelPositionEnd]">{{ contactForm.CONTACT_FORM_MESSAGE_LABEL }}</label>
        <div class="text-rose-900 px-4 mt-1" v-for="error in v$.formData.message.$errors" :key="error.$uid">{{ error.$message }}</div>
      </div>
    </div>

    <template #actions>
      <arrows-up class="w-8 h-8 cursor-pointer self-center" icon-name="Email address" icon-color="black" @click="goTopOfPage"/>
      <button type="button" class="submit-btn" :disabled="v$.$invalid" @click="send">{{ contactForm.CONTACT_FORM_SUBMIT_BTN }}</button>
    </template>
  </the-card>
</template>

<script>
import TheCard from "@/landing/components/UI/TheCard";
import ArrowsUp from "@/landing/components/UI/icons/ArrowsUp";
import {useVuelidate} from "@vuelidate/core";
import {helpers, required, email} from "@vuelidate/validators";

export default {
  name: "ContactForm",
  components: {ArrowsUp, TheCard},
  computed: {
    contactForm() {
      return this.$store.getters["contactFormData"];
    },
    labelPositionEnd() {
      return this.$store.getters["languageData"].direction === 'rtl'
          ? "right-1 peer-focus:right-1" : "left-4 peer-focus:left-4";
    },
  },
  data() {
    return {
      v$: useVuelidate(),
      formData: {
        fullName: null,
        email: null,
        message: null,
      },
    };
  },
  validations() {
    return {
      formData: {
        fullName: { required: helpers.withMessage(this.contactForm.CONTACT_FORM_FULL_NAME_REQUIRED, required) },
        email: {
          required: helpers.withMessage(this.contactForm.CONTACT_FORM_EMAIL_REQUIRED, required),
          email: helpers.withMessage(this.contactForm.CONTACT_FORM_EMAIL_NOT_VALID, email)
        },
        message: { required: helpers.withMessage(this.contactForm.CONTACT_FORM_MESSAGE_REQUIRED, required) },
      }
    };
  },
  methods: {
    goTopOfPage() {
      document.querySelector("body").scrollIntoView({ behavior: "smooth" })
    },
    async send() {
      const valid = await this.v$.$validate();
      if (!valid) return;

      this.$store.dispatch("submitContact", this.formData);
    }
  },
}
</script>

<style scoped>
.contact-card {
  @apply mt-3 bg-opacity-95;
}
.text-input {
  @apply block py-2.5 px-5 w-full text-gray-900 bg-transparent rounded-lg focus:outline-none focus:ring-0;
}
.text-input-border {
  border: 2px solid #d1d5db;
}
.text-input-border:focus {
  border: 2px solid #2563eb;
}
.text-label {
  @apply peer-focus:font-medium absolute text-sm text-gray-500 duration-300 transform -translate-y-6
  scale-75 top-3 origin-[0] peer-focus:text-blue-600 peer-focus:bg-slate-50 px-3 rounded-full
  peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6;
}
.submit-btn {
  @apply text-white bg-ham-dark-blue hover:bg-blue-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center;
}
.submit-btn[disabled] {
  @apply bg-slate-500
}
</style>