<template>
  <nav>
    <div v-if="isOpen" class="fixed inset-0 z-40" @click="isOpen = false"></div>
    <div class="fixed top-2 z-50" :class="[positionClasses]">
      <div v-if="isOpen" class="flex flex-col">
        <div class="bg-sky-700 bg-opacity-90 text-white p-2 my-1 rounded-lg flex items-center justify-center">
          <button type="button" v-for="language in languages" :key="language.id" class="mx-2" @click="selectLanguage(language)">
            <country-flag :country="language.flagCode" size="normal"/>
          </button>
        </div>
        <button type="button" class="bg-sky-700 bg-opacity-90 text-white p-2 my-1 rounded-lg flex items-center"
                :dir="stickySideBar.currentDirection" v-for="item in stickySideBar.navigations" :key="item.id" @click="goTop(item.id)">
          <component class="w-8 h-8" :is="item.icon"></component>
          <span :class="[marginClass]">{{ item.title }}</span>
        </button>
      </div>
      <div v-else>
        <setting-icon class="bg-sky-700 rounded-full p-1 w-10 h-10 cursor-pointer" icon-name="setting" @click="isOpen = true" />
      </div>
    </div>
  </nav>
</template>

<script>
import SettingIcon from "@/landing/components/UI/icons/SettingIcon";
import EducationIcon from "@/landing/components/UI/icons/EducationIcon";
import EngineerIcon from "@/landing/components/UI/icons/EngineerIcon";
import SkillsIcon from "@/landing/components/UI/icons/SkillsIcon";
import PortfolioIcon from "@/landing/components/UI/icons/PortfolioIcon";
import ContactIcon from "@/landing/components/UI/icons/ContactIcon";
import CountryFlag from "vue-country-flag-next";
import PersonIcon from "@/landing/components/UI/icons/PersonIcon";

export default {
  name: "StickySideBar",
  components: {PersonIcon, ContactIcon, PortfolioIcon, SkillsIcon, EngineerIcon, EducationIcon, SettingIcon, CountryFlag},
  data() {
    return {
      isOpen: false
    }
  },
  computed: {
    stickySideBar() {
      return this.$store.getters["stickySideBarData"];
    },
    positionClasses() {
      return this.stickySideBar.currentDirection === 'rtl' ? 'left-4' : 'right-4';
    },
    marginClass() {
      return this.stickySideBar.currentDirection === 'rtl' ? 'mr-2' : 'ml-2';
    },
    languages() {
      return this.$store.getters["languageBarData"];
    }
  },
  methods: {
    goTop(id) {
      this.isOpen = false;
      document.querySelector(id).scrollIntoView({ behavior: "smooth" });
    },
    selectLanguage(language) {
      this.isOpen = false;
      this.$store.dispatch("selectLanguage", language);
    }
  }
}
</script>