<template>
  <div class="mt-5 mb-2 flex flex-wrap justify-between items-center relative">
    <button v-if="email" type="button" class="group info-btn" @click="copyContent(email)">
      <email-icon class="w-8 h-8" icon-name="Email address"/>
      <span class="info-text" dir="auto">{{ email }}</span>
    </button>
    <button v-if="phoneNumber" type="button" class="group info-btn" @click="copyContent(phoneNumber)">
      <phone-icon class="w-8 h-8" icon-name="Phone number" />
      <span class="info-text" dir="auto">{{ phoneNumber }}</span>
    </button>
    <button v-if="address" type="button" class="group info-btn" @click="copyContent(address)">
      <location-icon class="w-8 h-8" icon-name="Location address" />
      <span class="info-text" dir="auto">{{ address }}</span>
    </button>
    <button v-if="status" type="button" class="group info-btn" @click="copyContent(status)">
      <availability-icon class="w-8 h-8" icon-name="Availability for job" />
      <span class="info-text" dir="auto">{{ status }}</span>
    </button>
    <button v-if="birthdate" type="button" class="group info-btn" @click="copyContent(birthdate)">
      <birth-day-icon class="w-8 h-8" icon-name="Birthdate" />
      <span class="info-text" dir="auto">{{ birthdate }}</span>
    </button>
    <button v-if="githubAddress" type="button" class="group info-btn" @click="copyContent(githubAddress)">
      <github-icon class="w-8 h-8" icon-name="githubAddress" />
      <span class="info-text" dir="auto">{{ githubAddress }}</span>
    </button>
    <button v-if="skypeId" type="button" class="group info-btn" @click="copyContent(skypeId)">
      <skype-icon class="w-8 h-8" icon-name="SkypeId" />
      <span class="info-text" dir="auto">{{ skypeId }}</span>
    </button>
    <button v-if="telegramId" type="button" class="group info-btn" @click="copyContent(telegramId)">
      <telegram-icon class="w-8 h-8" icon-name="TelegramId" />
      <span class="info-text" dir="auto">{{ telegramId }}</span>
    </button>
    <button v-if="instagramId" type="button" class="group info-btn" @click="copyContent(instagramId)">
      <instagram-icon class="w-8 h-8" icon-name="InstagramId" />
      <span class="info-text" dir="auto">{{ instagramId }}</span>
    </button>
    <button v-if="contactMeTitle" type="button" class="group contact-me-btn" @click="goToContactForm">
      <arrow-down class="w-8 h-8 bg-white rounded-full lg:bg-transparent" icon-color="black" icon-name="Go To Contact Me" />
      <span class="contact-me-text" dir="auto">{{ contactMeTitle }}</span>
    </button>
  </div>
</template>

<script>
import EmailIcon from "@/landing/components/UI/icons/EmailIcon";
import PhoneIcon from "@/landing/components/UI/icons/PhoneIcon";
import LocationIcon from "@/landing/components/UI/icons/LocationIcon";
import AvailabilityIcon from "@/landing/components/UI/icons/AvailabilityIcon";
import SkypeIcon from "@/landing/components/UI/icons/SkypeIcon";
import TelegramIcon from "@/landing/components/UI/icons/TelegramIcon";
import InstagramIcon from "@/landing/components/UI/icons/InstagramIcon";
import BirthDayIcon from "@/landing/components/UI/icons/BirthDayIcon";
import ArrowDown from "@/landing/components/UI/icons/ArrowDown";
import GithubIcon from "@/landing/components/UI/icons/GithubIcon";

export default {
  name: "InformationDetails",
  components: {
    GithubIcon, ArrowDown, BirthDayIcon, InstagramIcon, TelegramIcon, SkypeIcon, AvailabilityIcon, LocationIcon, PhoneIcon, EmailIcon
  },
  props: {
    email: {
      required: true,
      type: String
    },
    phoneNumber: {
      required: true,
      type: String
    },
    address: {
      required: true,
      type: String
    },
    status: {
      required: true,
      type: String
    },
    birthdate: {
      required: true,
      type: String
    },
    githubAddress: {
      default: '',
      type: String
    },
    skypeId: {
      default: '',
      type: String
    },
    telegramId: {
      default: '',
      type: String
    },
    instagramId: {
      default: '',
      type: String
    },
    contactMeTitle: {
      required: true,
      type: String
    },
    copyToClipboardTitle: {
      required: true,
      type: String
    },
    copyToClipboardMessage: {
      required: true,
      type: String
    }
  },
  methods: {
    copyContent(content) {
      navigator.clipboard.writeText(content);
      this.$store.dispatch("notify/addDefaultSuccess", {title: this.copyToClipboardTitle, message: this.copyToClipboardMessage});
    },
    goToContactForm() {
      document.querySelector("#contact").scrollIntoView({ behavior: "smooth" })
    }
  }
}
</script>

<style scoped>
.info-btn {
 @apply lg:w-full lg:flex lg:justify-between lg:items-center lg:bg-white lg:bg-opacity-30 lg:rounded-xl lg:py-2 lg:px-2 lg:my-1 lg:hover:bg-opacity-50;
}
.info-text {
  @apply w-full break-words hidden group-hover:block py-2 px-3 bg-black bg-opacity-90 rounded-2xl absolute top-10 left-1/2 transform -translate-x-1/2
  lg:block lg:flex-1 lg:w-10/12 lg:py-0 lg:px-0 lg:bg-transparent lg:static lg:translate-x-0 select-text;
}
.contact-me-btn {
  @apply lg:w-full lg:flex lg:justify-between lg:items-center lg:bg-white lg:bg-opacity-95 lg:rounded-xl lg:py-2 lg:px-2 lg:my-1
}
.contact-me-text {
  @apply hidden break-words w-full lg:flex-1 lg:block lg:w-10/12 lg:text-black
