import resolveConfig from 'tailwindcss/resolveConfig';
import tailwindConfig from '@/../tailwind.config';

const fullConfig = resolveConfig(tailwindConfig);

class BreakPoints {
    getBreakpointValue = (value) =>
        +fullConfig.theme.screens[value].slice(
            0,
            fullConfig.theme.screens[value].indexOf('px')
        );
    getCurrentBreakpoint = () => {
        let currentBreakpoint = "xs";
        let biggestBreakpointValue = 0;
        for (const breakpoint of Object.keys(fullConfig.theme.screens)) {
            const breakpointValue = this.getBreakpointValue(breakpoint);
            if (
                breakpointValue > biggestBreakpointValue &&
                window.innerWidth >= breakpointValue
            ) {
                biggestBreakpointValue = breakpointValue;
                currentBreakpoint = breakpoint;
            }
        }
        return currentBreakpoint;
    };

    isXs = this.getCurrentBreakpoint() === "xs";

    isSm =  this.getCurrentBreakpoint() === "sm";
    isSmAndUp = this.getBreakpointValue("sm") <= window.innerWidth;
    isSmAndDown = this.getBreakpointValue("sm") >= window.innerWidth;

    isMd = this.getCurrentBreakpoint() === "md";
    isMdAndUp = this.getBreakpointValue("md") <= window.innerWidth;
    isMdAndDown = this.getBreakpointValue("md") >= window.innerWidth;

    isLg = this.getCurrentBreakpoint() === "lg";
    isLgAndUp = this.getBreakpointValue("lg") <= window.innerWidth;
    isLgAndDown = this.getBreakpointValue("lg") >= window.innerWidth;

    isXl = this.getCurrentBreakpoint() === "xl";
}

const breakPoints = new BreakPoints();

export default breakPoints;