<template>
  <nav class="language-bar-card">
    <div v-for="language in languages" :key="language.id"
         class="mx-2 mb-1 px-2 cursor-pointer"
         @click="selectLanguage(language)">
      <country-flag :country="language.flagCode" size="normal"/>
    </div>
  </nav>
</template>

<script>
import CountryFlag from "vue-country-flag-next";

export default {
  name: "LanguageBar",
  components: {CountryFlag},
  computed: {
    languages() {
      return this.$store.getters["languageBarData"];
    }
  },
  methods: {
    selectLanguage(language) {
      this.$store.dispatch("selectLanguage", language);
    }
  }
}
</script>

<style>
.language-bar-card {
  @apply mx-3 mt-1 mb-2 px-2 rounded-md flex justify-center bg-white bg-opacity-10
}
</style>