<template>
  <div class="">{{text}}</div>
</template>

<script>
export default {
  name: "TextTyper",
  props: {
    text: {
      required: true,
      type: [String, Array],
      validator(value) {
        if (typeof value === 'string')
          return value.length > 0;
        return value.every(item => typeof item === 'string' && item.length > 0);
      }
    },
    repeat: {
      type: Number,
      default: Infinity,
      validator: value => value >= 0
    },
  }
}
</script>