<template>
  <div class="flex flex-wrap justify-center" id="portfolio">
    <div class="portfolio-card" v-for="portfolio in portfolios.list" :key="portfolio.id">
      <div class="relative text-black">
        <img class="rounded-t-md w-full max-h-80 mx-auto" :src="portfolio.image.content" :alt="portfolio.image.description">
        <p class="portfolio-title">{{ portfolio.title }}</p>
        <p class="portfolio-release-date">{{portfolios.PORTFOLIO_RELEASE_DATE}}: {{ portfolio.releaseDate }}</p>
      </div>
      <div class="flex flex-wrap justify-center">
        <span class="portfolio-tags" v-for="tag in portfolio.tags" :key="tag">{{ tag }}</span>
      </div>
      <p class="text-md text-black mt-5 text-justify indent-4 px-3">
        {{ portfolio.shortDesc }}
        <more-detail-icon v-if="portfolio.fullDesc || portfolio.medias.length > 0"
                          class="w-10 h-10 cursor-pointer hover:bg-[#632E99] hover:bg-indigo-200 hover:rounded-full p-2"
                          icon-name="More Details (full description)" icon-color="#14236C" @click="openMoreDetails(portfolio)"/>
      </p>
    </div>
    <the-modal v-model="toggleModal" :title="portfolios.MORE_DETAILS" :full-screen="$breakpoints.isSmAndDown">
      <p class="text-black px-4 mb-4 text-justify indent-4">{{ portfolioData.fullDesc }}</p>
      <portfolio-media :medias="portfolioData.medias" />
    </the-modal>
  </div>
</template>

<script>
import TheModal from "@/landing/components/UI/TheModal";
import MoreDetailIcon from "@/landing/components/UI/icons/MoreDetailIcon";
import PortfolioMedia from "@/landing/components/portfolio/PortfolioMedia";

export default {
  name: "PortfoliosInfo",
  components: {PortfolioMedia, MoreDetailIcon, TheModal},
  data() {
    return {
      toggleModal: false,
      portfolioData: null
    }
  },
  computed: {
    portfolios() {
      return this.$store.getters["portfolioData"];
    }
  },
  methods: {
    openMoreDetails(portfolio) {
      this.toggleModal = true;
      this.portfolioData = portfolio;
    }
  }
}
</script>

