.portfolio-card {
  @apply flex flex-col justify-between pb-3 w-full md:w-2/4 xl:w-1/4 m-1 rounded-lg bg-white;
}

.portfolio-title {
  @apply absolute w-[101%] py-2 top-0 left-1/2 transform -translate-x-1/2 rounded-t-lg text-center text-lg font-semibold backdrop-blur-sm backdrop-brightness-50;
}

.portfolio-release-date {
  @apply absolute w-[101%] py-2 bottom-0 left-1/2 transform -translate-x-1/2 text-center font-semibold backdrop-blur-sm backdrop-brightness-50;
}

.portfolio-tags {
  @apply text-black font-bold m-1 px-2 py-1 bg-gray-400 block flex-grow text-center rounded-xl;
}
