<template>
  <div>
    <div v-if="modelValue" class="fixed inset-0 z-40 opacity-40 bg-black" @click="closeModal"></div>
    <div v-if="modelValue" class="fixed z-50" :class="[modalFullScreen]">
      <div class="bg-white flex flex-col overflow-y-auto" :class="[cardFullScreen]">
        <div v-if="fullScreen" class="bg-blue-700 h-20 flex items-center px-4 py-2 mb-4">
          <close-icon icon-name="Close Modal" icon-color="#F2F2F2" class="w-10 h-10 cursor-pointer" @click="closeModal" />
          <div class="text-xl font-extrabold mx-3">{{ title }}</div>
        </div>
        <div v-else class="mb-4 px-4 flex justify-between">
          <div class="text-black text-xl font-extrabold">{{ title }}</div>
          <close-icon icon-name="Close Modal" icon-color="black" class="w-7 h-7 cursor-pointer" @click="closeModal" />
        </div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import CloseIcon from "@/landing/components/UI/icons/CloseIcon";
export default {
  name: "TheModal",
  components: {CloseIcon},
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
    },
    fullScreen: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    modalFullScreen() {
      return this.fullScreen ? "inset-0": "top-10 left-1/2 transform -translate-x-1/2 w-11/12";
    },
    cardFullScreen() {
      return this.fullScreen ? "h-screen w-full": "p-3 rounded-xl max-h-[91vh]";
    }
  },
  methods: {
    closeModal() {
      this.$emit('update:modelValue', false);
    }
  }
}
</script>