<template>
  <div class="mx-3 mt-5 mb-2 p-4 rounded-2xl flex flex-col" :class="[color]">
    <div class="text-lg font-bold mb-2">{{ notification.title }}</div>
    <div class="">{{ notification.message }}</div>
  </div>
</template>

<script>

export default {
  name: "NotificationMessage",
  props: {
    notification: {
      required: true
    }
  },
  computed: {
    color() {
      return this.notification.type === "success" ? "bg-green-700" : "bg-red-700";
    }
  },
  data() {
    return {
      timer: null,
    };
  },
  created() {
    this.timer = setTimeout(() => {
      this.$store.dispatch("notify/removeNotification", this.notification);
    }, this.notification.timeout);
  },
  beforeUnmount() {
    clearTimeout(this.timer);
  }
}
</script>